@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700;9..144,900&display=swap);

*{
  padding: 0;
  margin: 0;
  font-family: 'Barlow', sans-serif;
}

body{
  width: 100%;
}

.top-bg{
  background: url(/sunny-frontendmentor/static/media/image-header.ff7d0ec7.jpg) no-repeat center center;
  background-size: cover;
  height: 100vh;
}

.top-menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-conntent{
  background-color: hsl(168, 34%, 41%);
}
.logo{
    padding: 1.5em;
    color: white;
}
nav{
    display: flex;
    align-items: center;
    grid-gap: 1em;
    gap: 1em;
    padding: 1.5em;
}

nav>a{
    color: white;
    text-decoration: none;
}


.contact{
    background-color: white;
    padding: 1em;
    border-radius: 15px;
    color: black;
}

.title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5rem;
    color: white;
    grid-gap: 5rem;
    gap: 5rem;
    font-size: 2rem;
    font-family: 'Fraunces', serif;
    font-weight: 900;
}

.title-text{
    font-size: 5rem;
    font-family: 'Fraunces', serif;
    font-weight: bold;
    letter-spacing: 9px;
}
.parent {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.parent>div{
    display: flex;
    justify-content: center;
    align-items: center;
}

.img-box{
    width: 100%;
    height: 100%;
}
    
.div1 { grid-area: 1 / 1 / 2 / 2; }
.div2 { grid-area: 1 / 2 / 2 / 3; }
.div3 { grid-area: 2 / 1 / 3 / 2; }
.div4 { grid-area: 2 / 2 / 3 / 3; }
.div5 { grid-area: 3 / 1 / 4 / 2; }
.div6 { grid-area: 3 / 2 / 4 / 3; }

@media only screen and (max-width: 1000px){
    .parent {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(6, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        }
        
        .div1 { grid-area: 2 / 1 / 3 / 2; }
        .div2 { grid-area: 1 / 1 / 2 / 2; }
        .div3 { grid-area: 3 / 1 / 4 / 2; }
        .div4 { grid-area: 4 / 1 / 5 / 2; }
        .div5 { grid-area: 5 / 1 / 6 / 2; }
        .div6 { grid-area: 6 / 1 / 7 / 2; } 
   }
.article-text{
    padding: 10em;
}

.article-title{
    font-family: 'Fraunces', serif;
    font-size: 2.6rem;
}

.article-p{
    font-size: 1.1rem;
    line-height: 1.8em;
}

.article-learn{
    color: black;
    font-size: 1.3rem;
    font-weight: bold;
    position: absolute;
}

.red{
    text-decoration: none;
}

.red::after{
  content: '';
  height: 8px;
  background: red;  
  border-radius: 15px;
  display: block;
  position: relative;
  bottom: 8px;
  right: 5px;
  opacity: 0.3;
  width: 110%;
  text-align: center;
}

.yellow{
    -webkit-text-decoration-color: yellow;
            text-decoration-color: yellow;
}

.yellow::after{
    content: '';
    height: 8px;
    background: yellow;  
    border-radius: 15px;
    display: block;
    position: relative;
    bottom: 8px;
    right: 5px;
    opacity: 0.3;
    width: 110%;
    text-align: center;
  }
.background1{
    background-image: url(/sunny-frontendmentor/static/media/image-graphic-design.e5f5a3b7.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
}

.background2{
    background-image: url(/sunny-frontendmentor/static/media/image-photography.ed3c6053.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
}

.cardText{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
}

.cardText >p{
    margin: 2rem 10rem 5rem 10rem;
}

.card-title{
    font-family: 'Fraunces', serif;
    font-size: 1.9rem;
}

.card-text{
    line-height: 1.6;
    opacity: 0.5;
    
}
.client{
    text-align: center;
    padding: 10em;
}

.clinets-title{
    font-size: 1.3rem;
    letter-spacing:5px;
    font-family: 'Fraunces', serif;
}



.clients{
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 2.5em;
}

@media only screen and (max-width: 1000px){
    .clients {
        display: block;
        padding: 0;
    }
   }

.client-card{
    margin: 0 10px 0 10px;
}

.profile-image{
    border-radius: 50%;
    width: 4.3em;
    height: 4.3em;
    margin-bottom: 3em;
}

.comment-text{
    margin-bottom: 3.5em;
    line-height: 1.6;
    font-size: 1.2rem;
}

.client-card-name{
    font-size: 1.2rem;
    font-family: 'Fraunces', serif;
    margin-bottom: 0.5em;
}
.images-bottom {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    }
    
        .images-bottom1 { 
            grid-area: 1 / 1 / 2 / 2;
            
        }

        .images-bottom2 { 
            grid-area: 1 / 2 / 2 / 3;
          
        }

        .images-bottom3 { 
            grid-area: 1 / 3 / 2 / 4; 
           
        }

        .images-bottom4 {
            grid-area: 1 / 4 / 2 / 5; 
           
        } 

.fit{
    width: 100%;
    height: 100%;
}
.footer{
    text-align: center;
    padding: 5rem;
}

.footer-title{
    font-size: 3rem;
    font-family: 'Fraunces', serif;
    color: hsl(212, 27%, 19%);
    opacity: 0.6;
    font-weight: 900;
}

.footer-menu{
    display: flex;
    justify-content: center;
    grid-gap: 4em;
    gap: 4em;
    margin: 2em;
}

.footer-menu>a{
    text-decoration: none;
    font-size: 1.5rem;
    color: hsl(212, 27%, 19%);
    opacity: 0.6;
}
.social{
    display: flex;
    justify-content: center;
    grid-gap: 2em;
    gap: 2em;
    padding-bottom: 5rem;
}

.social-icon{
    width: 30px;
    height: 30px;
    color: hsl(212, 27%, 19%);
    opacity: 0.6;
}
